export default {
	data() {
		return {
			title: 'Hello',
			storeOpendid:'',
			itemlist:'',
			orderlist:{
				createTime:'000000000000000',
				updateTime:'000000000000000'
			}
		}
	},
	onLoad(item) {
		this.itemlist = item;
		console.log(item);
		this.storeOpendid = uni.getStorageSync('Opendid'); //加载缓存
		if (item) {
			
			if (!item.sparetwo) {
				this.orderDetails(item.addressid);
			} else{
				this.orderDetails(item.sparetwo);
			}
		} else{
			this.utils.success('暂无信息！',()=>{
				this.utils.navback();
			});
		}
	},
	
	methods: {
		orderDetails(e){
			this.utils.showloading();
			this.http.getApi('/shippings/GetShipping', {ShippingID:e }, 'get', this.storeOpendid)
			.then(res => {
				this.orderlist = res.data;
				console.log(res);
				uni.hideLoading();
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
				uni.showToast({
					title: err.msg,
					icon: "none",
					duration: 3000
				})
			});
		},
	}
}
